import {MainContext} from "../Main";
import {Link, useParams} from "react-router-dom";
import {React, useContext, useEffect, useState, useRef} from "react";

function Products() {

    const scrollNode = document.documentElement;
    scrollNode.scrollTop = 0

    const {productsCatalog, cartProducts, setCartProducts, bagSize} = useContext(MainContext);
    const inputRef = useRef();

    const params = useParams();
    const number = +params.number;

    const product = productsCatalog.filter(item => item.id === number)[0];

    const [currentBagSize, setCurrentBagSize] = useState(0);
    const [currentProductQuantity, setCurrentProductQuantity] = useState(1);

    useEffect(() => {

    }, [currentBagSize]);

    useEffect(() => {
        inputRef.current.value = (currentProductQuantity) ? currentProductQuantity : 1;
    }, [currentProductQuantity]);

    function getProductImg() {
        const productImg = "/" + product.img + "_800x.jpg";

        return (
           <img src={productImg} alt="#"/>
        );
    }

    function selectBagSize(event) {
        const selectBagSizeTmp = +event.currentTarget.dataset.id;
        if (selectBagSizeTmp >= 0) setCurrentBagSize(selectBagSizeTmp);

        event.currentTarget.parentNode.childNodes.forEach(node => {
                node.classList.remove("active");
            }
        )

        event.currentTarget.classList.add("active");
    }

    function getProductPrices() {
        return (
            product.prices.map((price, index) => {
                return (
                    <div onClick={selectBagSize} key={index} data-id={index}
                         className={(index === 0) ? "product__prices_bagPrice active" : "product__prices_bagPrice"}>
                        <div className="prices__bagPrice_bagSize">{bagSize[index] + " lb"}</div>
                        <div className="prices__bagPrice_Price">{"$" + price}</div>
                        <div className="prices__bagPrice_PriceLb">{"$" + (Math.round(price / bagSize[index]*100)/100) + "/lb"}</div>
                    </div>
                );
            })
        );
    }

    function addToCart() {
        if (!cartProducts) return;

        product.quantity = currentProductQuantity;
        product.bagSize = currentBagSize;

        let dublProduct = false;

        const cartProductsTmp = cartProducts.filter((item, index) => {
            if (item.id == product.id) {
                item.quantity += (currentProductQuantity > 0) ? currentProductQuantity : 1;
                dublProduct = true;
            }

            return item;
        });

        if (!dublProduct) cartProductsTmp.push(product);

        setCartProducts([...cartProductsTmp]);
    }

    function changeProductQuantity(quantity) {
        const quantityTmp = ((inputRef.current.value) ? +inputRef.current.value : 1) + quantity;

        setCurrentProductQuantity((quantityTmp) ? quantityTmp : 1);
    }

    function getProductRegion() {
        let region = "";

        product.region.forEach((item, index) => {
            region += (index === product.region.length - 1) ? item : item + " + ";
        })

        return region;
    }

    function getProductTastingNotes() {
        let tastingNotes = "";

        product.tastingNotes.forEach((item, index) => {
            tastingNotes += (index === product.tastingNotes.length - 1) ? item : item + ", ";
        })

        return tastingNotes;
    }

    return (
        <div className="page">

            <div className="container">

                <div className="product__wrapper">

                    <div className="product__image">
                        {getProductImg()}
                    </div>

                    <div className="product__prices">
                        <h1>{product.name}</h1>
                        <div className="product__prices_title">SELECT BAG SIZE</div>
                        <ul className="product__prices_select">
                            {getProductPrices()}
                        </ul>

                        <div className="product__prices_title">PRICE</div>
                        <div className="product__prices_totalPrice">
                            {"$" + Math.round(product.prices[currentBagSize] * currentProductQuantity * 100) / 100}
                        </div>

                        <div className="product__prices_title">QUANTITY</div>

                        <div className="product__prices_totalQuantity">
                            <input ref={inputRef} onChange={(e) => changeProductQuantity(0)} type="text" min="1"
                                   defaultValue={currentProductQuantity}/>
                            <button onClick={(e) => changeProductQuantity(-1)}
                                    className="prices__totalQuantity_btn-left">-
                            </button>
                            <button onClick={(e) => changeProductQuantity(1)}
                                    className="prices__totalQuantity_btn-right">+
                            </button>
                        </div>

                        <button onClick={addToCart} className="product__prices_addToCart">Add to cart</button>

                    </div>

                </div>

                <div className="product__description">

                    <h2 className="product__description_title">Description</h2>

                    <div className="product__description_content">
                        <p>This tribute to our uniformed service members - past and present  is an irresistible blend
                            of
                            light and medium roasts from all over the world. This rich, silky coffee features creamy
                            chocolate undertones and hints of spiced berry. With a smooth, lingering finish and sweet
                            flavor
                            profile, this brew will take your taste buds by storm, whether youre in uniform or not!
                        </p>

                        <ul className="product__description_characteristic">
                            {product.roast && <li><span>ROAST: </span>{product.roast}</li>}
                            {product.region && <li><span>REGION: </span> {getProductRegion()}</li>}
                            {product.acidity && <li><span>ACIDITY: </span> {product.acidity} </li>}
                            {product.body && <li><span>BODY: </span>{product.body}</li>}
                            {product.tastingNotes && <li><span>TASTING NOTES: </span>{getProductTastingNotes()}</li>}
                        </ul>
                    </div>

                </div>

            </div>
        </div>
    );
}

export default Products;